<template>
  <div>
    <v-header />
    <Carousel autoplay loop :autoplay-speed="5000" class="index-banner" :height="680" v-if="banner.length">
      <CarouselItem v-for="(item, index) in banner" :key="index">
        <img :src="item.indexUrl" :alt="item.indexName">
      </CarouselItem>
    </Carousel>
    <div class="index-product oh">
      <div class="web oh">
        <h2 class="index-title">热门产品推荐</h2>
        <ul class="product-list clearfix">
          <router-link tag="li" :to="`/web/pro_details?goodSn=${item.goodsSn}`" v-for="(item, index) in goods" :key="index">
            <div class="img-box">
              <img :src="item.goodsImg" alt />
            </div>
            <h3 class="name">{{item.goodsName}}</h3>
            <div class="price-sales clearfix">
              <div class="sales">
                <span>销售量：</span>
                <span>{{item.goodsSalesCount}}</span>
              </div>
              <div class="price">
                <small>￥</small>
                <span>{{item.shopPrice}}</span>
              </div>
            </div>
          </router-link>
        </ul>
      </div>
    </div>
    <v-footer />
  </div>
</template>
<script>
export default {
  data() {
    return {
        banner: [],
        goods: []
    };
  },
  created(){
      this.getBanner();
      this.getGoods();
  },
  methods: {
    //   获取banner
    getBanner() {
      this.$api
        .getIndexBanner()
        .then(res => {
          this.banner = res.data.data;
        })
        .catch(err => console.log(err));
    },
    // 获取商品
    getGoods(){
      this.$api
        .getIndexGoods()
        .then(res => {
          this.goods = res.data.data.records;
        })
        .catch(err => console.log(err));
    },
    // 获取购物车
    getShopingCart(){

    }
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/mixins.less";
.index-banner {
  overflow: hidden;
  .ivu-carousel-item{
    position: relative;
    overflow: hidden;
    img{
      .center();
      max-width: none;
      min-width: 100%;
      max-height: 100%;
    }
  }
}
.index-title {
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  position: relative;
  padding-bottom: 15px;
  margin: 50px 0 40px;

  &::after {
    content: "";
    .xcenter();
    bottom: 0;
    width: 50px;
    height: 1px;
    background-color: @fontColor;
  }
}
.index-product {
  padding: 100px 0 80px;
  background-color: @grayBg;

  .web {
    background-color: #fff;
  }

  .product-list {
    padding: 0 20px;

    li {
      float: left;
      padding: 0 20px;
      width: 20%;
      margin-bottom: 40px;
    }

    .img-box {
      width: 100%;
      position: relative;
      height: 252px;
      overflow: hidden;

      img {
        width: 100%;
        .center();
        transform: translate3d(-50%, -50%, 0) scale3d(1, 1, 1);
        .transition(0.5s);
      }
    }

    .name {
      display: block;
      font-size: 16px;
      line-height: 40px;
    }

    .price-sales {
      .price {
        float: left;
        width: 49%;
        color: @themeColor;
      }

      .sales {
        font-size: 12px;
        color: #999;
        float: right;
        width: 49%;
        text-align: right;
      }
    }
    li:hover {
      .img-box img {
        transform: translate3d(-50%, -50%, 0) scale3d(1.2, 1.2, 1);
      }

      .name,
      .price-sales .sales {
        color: @themeColor;
      }
    }
  }
}
</style>



